<template>
	<dt-scroll-box class="myBacklog-page">
		<div slot="top"></div>
		<div slot="main-content">
			<!-- 我的待办  界面-->
			<div class="myBacklog">
				<!-- 头部 -->
				<div class="head clearfix">
					<ul class="fl">
						<li style="background:#17844B">
							<span class="info">待办</span>
							<span class="count">
								<p v-text='waitCount'></p>
							</span>
						</li>
					</ul>
				</div>

				<!-- 表格部分 -->
				<!-- 表格 -->
				<el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange"
				 border @row-click="openDetails" :header-cell-style="{background:'#EBEBEB'}">

					<el-table-column prop="applyNum" label="单号"></el-table-column>
					<el-table-column prop="type" label="类型" :formatter="typeFormat"></el-table-column>
					<el-table-column prop="name" label="节点名称" :formatter="nodeFormat"></el-table-column>
					<el-table-column prop="time" label="申请日期"></el-table-column>
					<el-table-column prop="person" label="申请人"></el-table-column>
				</el-table>

			</div>
		</div>
	</dt-scroll-box>
</template>
<script>
	/*
	 *我的待办  界面
	 * @author renchao 2019-02-26
	 */
	export default {
		data() {
			return {
				// 分页
				currentPage: 1,
				// 搜索框
				searchData: "",
				waitCount: 0,
				// 表格
				tableData: [],
				multipleSelection: [],
				// 绑定表单数据
				formData: {
					username: "",
					password: "",
					email: "",
					mobile: ""
				},

				editUserDialogFormVisible: false
			};
		},
		created() {
			this.$dt.getAjax(
				'/project/list/approveSrpmsProject', {},
				(res) => {

					this.tableData = res;
					this.waitCount = this.tableData.length;
				},
				() => {

				}
			)
		},
		methods: {
			
			typeFormat(row, column) {
			
				return "评审";
			},
			nodeFormat(row, column) {
			
				return "评审中";
			},
			//   点击表格每行跳转
			//打开详情页弹窗
			openDetails(row) {
				console.log(row.id);
				this.$router.push({
					path: '/proApplyCheck',
					query: {
						orderId: row.id
					}
				});
			},
			// 关闭按钮
			handleClosed() {
				for (const key in this.formData) {
					this.formData[key] = "";
				}
			},
			// 表格
			toggleSelection(rows) {
				if (rows) {
					rows.forEach(row => {
						this.$refs.multipleTable.toggleRowSelection(row);
					});
				} else {
					this.$refs.multipleTable.clearSelection();
				}
			},
			handleSelectionChange(val) {
				this.multipleSelection = val;
			},
			// 分页
			handleSizeChange(val) {
				console.log(`每页 ${val} 条`);
			},
			handleCurrentChange(val) {
				console.log(`当前页: ${val}`);
			}
		}
	};
</script>
<style lang="less">
	.myBacklog-page {
		padding-bottom: 60px;

		//   头部
		.head {
			margin-bottom: 30px;

			ul li {
				position: relative;
				//   height: 125px;
				//   width: 125px;
				//   border-radius: 7px;
				margin-right: 20px;
				float: left;
				font-size: 16px;
				color: #fff;

				//小屏
				@media screen and(max-width: 1599px) {
					height: 90px;
					width: 90px;
					border-radius: 6px;
				}

				//大屏
				@media screen and(min-width:1600px) {}

				height: 125px;
				width: 125px;
				border-radius: 7px;
			}

			.info,
			.count {
				position: absolute;
			}

			.info {
				top: 10px;
				right: 10px;
			}

			.count {
				bottom: 10px;
				left: 10px;
				font-weight: 700;
			}
		}

		//搜索框
		.search {
			width: 29%;
			margin-right: 20px;

			//小屏
			@media screen and(max-width: 1599px) {
				margin-left: 10px;
			}

			//大屏
			@media screen and(min-width:1600px) {
				margin-left: 20px;
			}
		}

		// 按钮
		.dt-btn {
			height: 40px;
		}

		// 表格部分
		// 分页
		.paging {
			margin-top: 20px;
			margin-left: 20%;
		}
	}
</style>



